import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { getProjects, archiveProject } from '../../api/api';
import { motion } from 'framer-motion';

function ProjectList() {
    const [projects, setProjects] = useState([]);

    useEffect(() => {
        const fetchProjects = async () => {
            const response = await getProjects();
            setProjects(response.data);
        };

        fetchProjects();
    }, []);

    const handleArchiveProject = async (projectId) => {
        try {
            await archiveProject(projectId); //  API   
            //     
            setProjects(projects.map(project => 
                project.id === projectId ? { ...project, status: 'ARCHIVED' } : project
            ));
        } catch (error) {
            console.error('Error archiving project:', error);
            alert('Error archiving project: ' + (error.response?.data?.message || 'Unknown error'));
        }
    };

    return (
        <div className="container mx-auto p-4">
            <h2 className="text-3xl font-bold mb-4">Projects</h2>
            <motion.ul
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                transition={{ duration: 0.5 }}
                className="space-y-4"
            >
                {projects.map((project) => (
                    <motion.li
                        key={project.id}
                        whileHover={{ scale: 1.02 }}
                        className="bg-white p-4 rounded-lg shadow-md"
                    >
                        <Link to={`/projects/${project.id}`} className="text-blue-600 hover:underline">
                            <h3 className="text-xl font-bold">{project.name}</h3>
                        </Link>
                        <p className="text-gray-600">{project.description}</p>
                        <p>Status: {project.status}</p>
                        <p>Completion: {project.completionPercentage}%</p>

                        {/*  "" */}
                        {project.status !== 'ARCHIVED' && (
                            <button
                                onClick={() => handleArchiveProject(project.id)}
                                className="mt-2 bg-red-600 text-white p-2 rounded hover:bg-red-700"
                            >
                                 (ARCHIVED)
                            </button>
                        )}
                    </motion.li>
                ))}
            </motion.ul>
        </div>
    );
}

export default ProjectList;